body {
    padding: 0;
    margin: 0;
    background-color: rgb(112, 13, 112);
}
.todolist {
    width: 600px;
    min-height: 500px;
    margin: 50px auto;
    padding: 30px;
    border-radius: 10px;
    background: linear-gradient(
        #008F7A,
        #00817D,
        #00737A,
        #136473,
        #265667,
        #2F4858
        );
    z-index: 11111 !important;
}

.todolist input {
    margin-left: 20px;
    border: none;
    height: 40px;
    width: 400px;
    background: #136473;
    border-radius: 5px;
    color: white;
    padding: 0 20px;
}

.todolist input::-webkit-input-placeholder{
    font-size: 18px;
    color: rgba(255,255,255,0.6);
}

.todolist input:-ms-input-placeholder{
    font-size: 18px;
    color: rgba(255,255,255,0.6);
}

.todolist input::-ms-input-placeholder{
    font-size: 18px;
    color: rgba(255,255,255,0.6);
}

.todolist input::placeholder{
    font-size: 18px;
    color: rgba(255,255,255,0.6);
}

.todolist input:focus {
    outline: none;
}

/* .todolist .btn {
    width: 100%;
    height: 50px; 
     margin-left: 20px;
} */

.todolist .second_button {
    display: none;
}

@media (max-width: 587px){
    .todolist {
        width: 300px;
        height: 200px;
    }

    .todolist .first_button {
        display: none;
    }

    .todolist input {
        width: 100%;
        margin-left: unset;
        text-align: center;
    }

    .todolist .second_button {
        display: block;
        width: 100%;
    }
}
.list {
    background: #c94b4b;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #4b134f, #c94b4b); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    
    border-radius: 5px;
    margin: 10px;
}

.list p {
    color: white;
    padding: 1rem;
    position: relative;
}
.list p span {
    position: absolute;
    right: 20px;
    cursor: pointer;
}
.card img {
    height: 300px !important;
}
